.welcome-container{display:flex;flex-direction:row;animation:popup .3s linear;background-color:#060f1d;height:100%;width:95%;margin:0 auto}@keyframes popup{0%{opacity:0}100%{opacity:1}}.arrow{margin-right:5px;color:teal}.introduction{margin:0 25px}.single-tool{display:flex;flex-direction:row;padding:5px}.tool-container{display:grid;grid-template-columns:33% 33% 33%}.tools{width:50%;margin:0 25px;white-space:nowrap}.hello{font-size:clamp(25px, 2.5vw, 40px);white-space:nowrap}.specialize{font-size:clamp(18px, 1vw, 20px)}@media screen and (max-width: 900px){.welcome-container{flex-direction:column;align-items:center}.introduction{margin-top:25px}.hello{font-size:40px}.specialize{font-size:27px}.tools{width:90%;margin:15px 0}.tools>h3{text-align:center}.tool-container{grid-template-columns:25% 25% 25% 25%;width:100%;margin-left:7vw}}@media screen and (max-width: 900px)and (max-width: 900px){.hello{font-size:5vw}.specialize{text-align:center}}@media screen and (max-width: 900px)and (max-width: 600px){.tools{width:100%}}
a{text-decoration:none;color:#fff}.contact-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;height:100%;animation:popup .3s linear;background-color:#060f1d}@keyframes popup{0%{opacity:0}100%{opacity:1}}.link-container{display:flex;flex-direction:row;justify-content:space-evenly;width:50%}.contact-link{border:2px solid #fff;padding:5px 10px;border-radius:5px;font-weight:100;transition:.5s;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.contact-link:hover{background-color:#fff}.contact-link:hover>a{color:#0a192f !important}.contact-link:hover>img{-webkit-filter:contrast(0%) brightness(0%);filter:contrast(0%) brightness(0%)}a:hover{color:#0a192f !important}.get-in-touch{font-weight:900}.contact-icon{width:15px;-webkit-filter:contrast(0%) brightness(500%);filter:contrast(0%) brightness(500%);margin-right:5px}
