.portfolio-container{display:grid;grid-template-columns:15% 85%;animation:popup .3s linear;background-color:#060f1d;height:100%;width:95%;margin:0 auto}@keyframes popup{0%{opacity:0}100%{opacity:1}}.portfolio-image{width:400px;height:215px;border:7px solid #5e5c5c;padding-top:5px;border-radius:5px;transition:.1s;background-color:#5e5c5c;box-shadow:0px 0px 0px 2px #000}.color-block{height:130px;width:69px;background-color:#5e5c5c;border-radius:9px;border:2px solid #000;transform:rotate(0deg) skew(0deg);-webkit-transform:rotate(0deg) skew(0deg);margin-right:-50px;margin-top:75px;z-index:10}.portfolio-item{display:flex;flex-direction:row;margin-top:15px;margin:0 auto;padding-top:25px}.portfolio-links{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;flex-wrap:wrap;width:300px}.portfolio-links>a{padding:2px;transition:.2s}.portfolio-links>a:hover{opacity:.3 !important;color:#fff !important}.project-nav{padding:5px 10px;border:2px solid #22344e;margin:5px 0;width:150px;text-align:center;color:#fff;border-radius:5px;cursor:pointer;transition:.2s}.project-nav:hover{background-color:#22344e}.project-nav-title{padding:5px 10px;margin:5px 0;width:150px;text-align:center;color:#fff;font-weight:900}.recent-projects{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;margin-left:25px}.active{background-color:#22344e;padding:5px 10px;border:2px solid #22344e;margin:5px 0;width:150px;text-align:center;border-radius:5px;cursor:pointer}.portfolio-desc{margin-left:-150px;background-color:#22344e;padding:25px;width:400px;margin-top:50px}.desc-cont{z-index:100}.phone-image{width:60px;margin-top:4px;margin-left:4px;border-radius:5px;height:120px;border:1px solid #000}@media screen and (max-width: 1100px){.portfolio-container{width:100%}.portfolio-item{width:70%}}@media screen and (max-width: 1000px){.project-nav-title{display:none}.portfolio-container{display:flex;flex-direction:column}.recent-projects{flex-direction:row;width:50%;display:flex;justify-content:space-between;margin:25px auto 0 auto}.portfolio-item{margin-top:25px;width:100%;justify-content:center}}@media screen and (max-width: 750px){.color-block{margin:0;margin-bottom:-210px;margin-left:-400px}.desc-cont{z-index:1}.portfolio-desc{margin:0;margin-top:-20px;background-color:rgba(34,52,78,.9);z-index:1;width:550px}.portfolio-image{width:350px;height:190px}.portfolio-item{flex-direction:column;justify-content:center;align-items:center;margin-top:70px}.portfolio-links{width:100%;margin-top:10px}.recent-projects{width:65%;margin-top:10px}}@media screen and (max-width: 650px){.portfolio-desc{padding:20px;padding-top:20px}}
